.title {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding: 150px;
    background: url(./../../../../static/images/orders-bg.jpg) no-repeat center;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 110px;
}

.title > span {
    color: white;
    font-weight: 700;
    font-size: 36px;
    position: absolute;
    z-index: 2;
}

.title:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.4;
    z-index: 0;
}

.marginTop {
    margin-top: 290px;
}

.activeOrderWrapper {
    position: relative;
    width: 70%;
    margin: 0 auto 20px;
    padding: 0 10px;
}

.completedOrdersWrapper {
    text-align: left;
    width: 70%;
    margin: 0 auto;
    padding: 0 10px;
}

.tableTitle,
.completedOrdersTitle {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 20px;
}

.buttonWrapper {
    text-align: right;
}

.buttonWrapper > div {
    display: inline-block;
}

@media screen and (max-width: 1199px) {
    .title {
        padding: 100px;
    }

    .marginTop {
        margin-top: 190px;
    }
}
@media screen and (max-width: 991px) {
    .marginTop {
        flex-direction: column;
        align-items: center;
    }

    .activeOrderWrapper {
        width: 100%;
    }

    .completedOrdersWrapper {
        width: 100%;
    }
}
@media screen and (max-width: 767px) {
    .title {
        padding: 80px;
    }

    .marginTop {
        margin-top: 140px;
    }
}
@media screen and (max-width: 480px) {
    .title {
        padding: 60px;
        margin-top: 85px;
    }

    .marginTop {
        margin-top: 110px;
    }

    .title > span {
        font-size: 32px;
    }

    .dataItemTitle {
        font-size: 16px;
    }

    .activeOrderWrapper,
    .completedOrdersWrapper {
        padding: 0;
    }
}
